@import "normalize.css";
.wrapper {
  min-width: 320px;
  max-width: 100%;
  margin: 0 auto;
}
.wrapper-content {
  max-width: 1150px;
  margin: 0 auto;
}
.container {
  width: 260px;
  margin: 0 auto;
}
@media (min-width: 772px) {
