}
.btn {
  display: block;
  width: 255px;
  margin: 0 auto;
  padding-top: 13px;
  padding-bottom: 15px;
  border: solid 2px #444444;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.18;
  color: #444444;
  transition: 0.2s;
}
.btn:hover {
  color: #ffffff;
  background-color: #444444;
}
.btn:active {
  color: rgba(255, 255, 255, 0.3);
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.menu-close {
  display: none;
}
.menu-show {
  display: block;
}
.page-header {
  position: relative;
}
.page-header__logo img {
  display: block;
  margin: 0 auto;
  padding-top: 22px;
  padding-bottom: 20px;
}
.page-header__logo:hover img {
  opacity: 0.6;
}
.page-header__logo:active img {
  opacity: 0.3;
}
.page-header__burger {
  position: absolute;
  top: 32px;
  right: 30px;
  display: block;
  width: 20px;
  height: 14px;
  border: none;
  background-image: url("../img/icon-menu-open.svg");
  background-repeat: no-repeat;
  cursor: pointer;
}
@media (min-width: 772px) {
  .page-header__logo {
    justify-content: flex-start;
    margin-bottom: 20px;
  }
  .page-header__logo img {
    margin: 0;
  }
  .page-header__burger {
    display: none;
  }
}
@media (min-width: 1150px) {
  .page-header__logo img {
    padding-top: 9px;
    padding-bottom: 7px;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
}
.main-nav__list li {
  display: block;
  list-style: none;
  box-shadow: 0 1px 0 0 #ececec, inset 0 1px 0 0 #ececec;
}
.site-list__item a {
  display: inline-block;
  padding-top: 28px;
  padding-bottom: 28px;
  padding-left: 45px;
  vertical-align: middle;
  text-decoration: none;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: bold;
  line-height: 1.41;
  color: #000000;
}
.site-list__item a:hover {
  opacity: 0.6;
}
.site-list__item a:hover + svg {
  fill: #63d1bb;
}
.site-list__item--search svg:hover {
  fill: #63d1bb;
}
.site-list__item a:active,
.site-list__item a:active + svg,
.site-list__item--search svg:active {
  opacity: 0.3;
}
.site-list__item--search,
.site-list__item--basket {
  position: relative;
}
.site-list__item--search svg,
.site-list__item--basket svg {
  position: absolute;
  top: 32px;
  left: 10px;
  fill: #d2cbc3;
  cursor: pointer;
}
.main-nav__list--delivery {
  display: none;
  margin: 0;
  padding-top: 24px;
  padding-right: 52px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 1.6;
  text-align: right;
}
.main-nav__list--bottom {
  display: none;
}
@media (min-width: 772px) {
  .main-nav {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    z-index: 10;
  }
  .main-nav__list {
    display: flex;
    margin: 0;
    padding: 0;
    z-index: 10;
  }
  .main-nav__list li {
    box-shadow: none;
  }
  .main-nav__list--left {
    order: 2;
  }
  .main-nav__list--right {
    justify-content: flex-end;
    width: 397px;
    height: 88px;
  }
  .site-list__item a {
    padding: 0;
    font-size: 15px;
  }
  .site-list__item--catalog {
    margin-right: 37px;
  }
  .site-list__item--basket {
    padding-top: 30px;
    padding-bottom: 33px;
    padding-left: 83px;
    border-left: 1px solid #ececec;
  }
  .site-list__item--basket a {
    font-size: 17px;
  }
  .site-list__item--search {
    width: 82px;
    border-left: 1px solid #ececec;
  }
  .site-list__item--search a {
    font-size: 0;
  }
  .site-list__item--search,
  .site-list__item--basket {
    position: relative;
  }
  .site-list__item--search svg,
  .site-list__item--basket svg {
    position: absolute;
    top: 35px;
    left: 32px;
  }
  .main-nav__list--delivery {
    order: 3;
    z-index: 10;
  }
  .main-nav__list--bottom {
    display: none;
  }
  .main-nav__list--bottom a {
