.category {
  width: 100%;
  margin-bottom: 26px;
  padding: 0;
}
.category__item {
  list-style: none;
  background-color: #63d1bb;
  transition: 0.2s;
}
.category__item a {
  display: block;
  width: 106px;
  padding-top: 28px;
  padding-bottom: 30px;
  padding-left: 28px;
  text-decoration: none;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 19px;
  font-weight: bold;
  line-height: 1.26;
  color: #ffffff;
  transition: 0.2s;
}
